.faq-page__section:not(:first-child) {
  margin-top: 60px;
}

@media (min-width: 992px) {
  .faq-page__section:not(:first-child) {
    margin-top: 0;
  }
}

.faq-page__section-title {
  margin-bottom: 50px;
}

/**
 * Styles for the FAQ topics block.
 */
.faq-topics__item {
  margin-top: 15px;
}

.faq-topics__link {
  position: relative;
  display: inline-block;
  padding-left: 12px;
  color: #959595;
  font-family: "PT Serif", serif;
  font-size: 14px;
  /* stylelint-disable-next-line declaration-block-no-duplicate-properties */
  font-size: 0.875rem;
  text-decoration: none;
  transition: all 0.25s linear;
}

.faq-topics__link:before {
  content: "";
  left: 0;
  position: absolute;
  top: 8px;
  display: block;
  height: 4px;
  width: 4px;
  background: #959595;
  border-radius: 50%;
}

.faq-topics__link:hover, .faq-topics__link:focus {
  color: #252525;
  text-decoration: underline;
}

.faq-topics__link:hover:before, .faq-topics__link:focus:before {
  background: #252525;
}

/**
 * Styles for the accordion block.
 */
.accordion__toggle {
  align-items: center;
  display: flex;
  margin-top: 10px;
  padding: 15px;
  width: 100%;
  color: #959595;
  font-family: "Montserrat", sans-serif;
  font-size: 14px;
  /* stylelint-disable-next-line declaration-block-no-duplicate-properties */
  font-size: 0.875rem;
  background: transparent;
  border: 1px dashed #959595;
  cursor: pointer;
  transition: all 0.25s linear;
}

.accordion__toggle:hover, .accordion__toggle:focus {
  color: #eb55a3;
  border-color: #eb55a3;
}

.accordion__header_active .accordion__toggle {
  color: #fff;
  background: #eb55a3;
  border: 0;
}

.accordion__header_active .accordion__toggle:hover, .accordion__header_active .accordion__toggle:focus {
  color: #fff;
  background: #252525;
  border: 0;
}

.accordion__title {
  margin-right: auto;
  text-transform: uppercase;
}

.accordion__icon {
  margin-left: 30px;
}

.accordion__icon_open {
  display: block;
}

.accordion__header_active .accordion__icon_open {
  display: none;
}

.accordion__icon_close {
  display: none;
}

.accordion__header_active .accordion__icon_close {
  display: block;
}

.accordion__content {
  display: none;
  padding: 15px 18px;
  font-family: "PT Serif", serif;
}

/**
 * Styles for the under-construction block.
 */
.under-construction {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  text-align: center;
}

.under-construction__header {
  padding: 32px 15px;
}

.under-construction__logo {
  max-width: 100%;
}

.under-construction__main {
  display: flex;
  flex-direction: column;
  flex: 1 1 auto;
  justify-content: center;
  margin: 0 auto;
  max-width: 740px;
  padding: 10px 15px 50px;
  width: 100%;
}

@media (min-width: 768px) {
  .under-construction__main {
    padding-top: 50px;
  }
}

.under-construction__title {
  color: #252525;
  font-size: 36px;
